html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
p {
  margin: 0;
}

.container {
  width: 80%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}
.App {
  background-color: rgba(255, 255, 255, 0.6);
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: rgb(122, 120, 120);
  -webkit-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: rgb(179, 120, 55);
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  width: 200px !important;
  height: 100vh;
}

/* General sidebar styles */
.bm-menu {
  background: rgb(243, 155, 68);
  padding: 1rem 0;
  font-size: 1.5rem;
  overflow-y: hidden !important;
}

/* Wrapper for item list */
.bm-item-list {
  padding: 0;
  text-align: center;
}

/* Individual item */
.bm-item {
  display: inline-block;
  margin-top: 2rem;
  color: rgb(201, 201, 201);
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

@media (max-width: 1200px) {
  .bm-menu-wrap {
    width: 150px !important;
    height: 50vh;
  }

  .bm-item {
    margin-top: 1.4rem;
  }
}

.home {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: rgb(44, 44, 66, 0.9);
  align-items: center;
  height: 100vh;
  margin-bottom: 5rem;
}
.home-image {
  height: auto;
  width: 100%;
  -webkit-animation: revealContent 2.5s ease-in;
          animation: revealContent 2.5s ease-in;
}
.home-content {
  align-self: center;
  text-align: center;
  -webkit-animation: slideContent 2s linear;
          animation: slideContent 2s linear;
  font-size: 2.2rem;
  color: rgba(255, 255, 255, 0.7);
  text-shadow: 0.5rem 0.5rem 0.6rem rgba(0, 0, 0, 0.7);
}
.home-name {
  color: rgb(214, 214, 15);
}
.home-intro {
  margin-bottom: 2.5rem;
}

.home-description {
  font-size: 1.4rem;
}
.arrow {
  position: relative;
  padding: 10px 20px;
  font-size: 2rem;
  cursor: pointer;
  -webkit-transition: all 0.6s ease-out;
  transition: all 0.6s ease-out;
}

.arrow:hover {
  background-color: white;
  border-radius: 50%;
  box-shadow: 0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.4);
}
.arrow:hover .bounce {
  color: rgb(0, 0, 0);
}

.bounce {
  -webkit-animation: bounce 1.3s infinite;
          animation: bounce 1.3s infinite;
  margin-top: 3rem;
  color: rgb(255, 255, 255, 0.8);
}

@-webkit-keyframes bounce {
  0%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 0.4;
  }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 1;
  }
}

@keyframes bounce {
  0%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 0.4;
  }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 1;
  }
}

@-webkit-keyframes revealContent {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 20;
  }
  80% {
    opacity: 60;
  }
  100% {
    opacity: 1;
  }
}

@keyframes revealContent {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 20;
  }
  80% {
    opacity: 60;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes slideContent {
  0% {
    -webkit-transform: translateY(200px);
            transform: translateY(200px);
    opacity: 0;
  }
  25% {
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
    opacity: 0.2;
  }
  50% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 0.6;
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slideContent {
  0% {
    -webkit-transform: translateY(200px);
            transform: translateY(200px);
    opacity: 0;
  }
  25% {
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
    opacity: 0.2;
  }
  50% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 0.6;
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@media (max-width: 1200px) {
  .home {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: rgb(44, 44, 66, 0.9);
    margin-bottom: 0;
  }
}

@media (max-width: 600px) {
  .home {
    display: grid;
    grid-template-columns: 1fr;
    background-color: rgb(44, 44, 66, 0.9);
    height: auto;
  }
}

.feature {
  box-shadow: 0.4rem 0.4rem 0.2rem rgba(0, 0, 0, 0.3);
  padding: 0.2rem 0;
  border-radius: 40px;
  height: 18rem;
}
.icon {
  width: 40%;
  margin: 0 auto;
  padding: 2rem 0;
  font-size: 2rem;
  text-align: center;
  background-color: rgb(179, 120, 55);
  -webkit-clip-path: polygon(
    20% 0%,
    80% 0%,
    100% 20%,
    100% 80%,
    80% 100%,
    20% 100%,
    0% 80%,
    0% 20%
  );
  clip-path: polygon(
    20% 0%,
    80% 0%,
    100% 20%,
    100% 80%,
    80% 100%,
    20% 100%,
    0% 80%,
    0% 20%
  );
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}
.icon:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  cursor: pointer;
}
.fas {
  color: rgba(255, 255, 255, 0.9);
}
.content {
  text-align: center;
  font-size: 1.2rem;
  line-height: 1.3;
}

.paragraph {
  justify-content: center;
}

@media (max-width: 1200px) {
  .react-reveal:last-child {
    grid-column-start: 2;
    grid-column-end: 3;
  }
}

@media (max-width: 400px) {
  .react-reveal:last-child {
    grid-column-start: auto;
    grid-column-end: auto;
  }

  .icon {
    width: 40%;
    margin: 0 auto;
    padding: 1.4rem 0;
    font-size: 2rem;
    text-align: center;
  }
}

.progress-bar {
  position: relative;
  height: 40px;
  width: 90%;
  margin: 0 auto;
  border: 4px solid rgba(0, 0, 0, 0.6);
  margin-top: 2rem;
  overflow: hidden;
  border-radius: 40px;
  box-shadow: 0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.5);
}

.filler {
  background: rgb(179, 120, 55);
  height: 100%;
  border-radius: inherit;
  -webkit-transition: width 0.2s ease-in;
  transition: width 0.2s ease-in;
}

.tools {
  position: absolute;
  font-size: 1.05rem;
  width: 100%;
  text-align: center;
  height: 100%;
  color: rgba(255, 255, 255, 0.7);
  -webkit-transform: translateY(25%);
          transform: translateY(25%);
  overflow: hidden;
}

#about {
  width: 100%;
}
.about {
  width: 85%;
  margin: 0 auto;
  padding: 3rem;
  height: auto;
}

.header {
  font-size: 3rem;
  text-align: center;
  text-shadow: 0.5rem 0.5rem 0.6rem rgba(0, 0, 0, 0.5);
}

.header-bar {
  width: 5rem;
  height: 7px;
  text-align: center;
  background-color: rgb(179, 120, 55);
  margin: 0 auto;
}

.header.active {
  -webkit-animation: slideFromLeft 1s ease-out;
          animation: slideFromLeft 1s ease-out;
}
.features-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 3rem 4rem;
  margin-top: 4rem;
  justify-content: center;
}
.details {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-items: center;
}

.introduction-img {
  margin: 0 auto;
  width: 60%;
  text-align: center;
}
.introduction-img img {
  width: 60%;
  border-radius: 50%;
  border: 4px solid #b32f1b;
}
.introduction-info {
  width: 60%;
  margin: 2rem auto 2rem auto;
}
.introduction-text {
  font-size: 1.2rem;
  text-align: center;
  font-weight: 400;
}
.skills {
  padding-top: 6rem;
  width: 100%;
  margin: 0 auto;
}
@-webkit-keyframes slideFromLeft {
  0% {
    -webkit-transform: translateX(-400px);
            transform: translateX(-400px);
  }
  50% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes slideFromLeft {
  0% {
    -webkit-transform: translateX(-400px);
            transform: translateX(-400px);
  }
  50% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@media (max-width: 1200px) {
  .features-container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 800px) {
  .features-container {
    grid-template-columns: repeat(2, 1fr);
  }

  .details {
    grid-template-columns: 1fr;
  }
  .skills {
    padding-top: 1rem;
  }
}

@media (max-width: 400px) {
  .features-container {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}

.projects {
  padding-top: 4rem;
  margin-top: 4rem;
  margin-bottom: 12rem;
}
.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  display: flex;
  justify-content: center;
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.7);
}

.react-tabs__tab {
  display: inline-block;
  position: relative;
  list-style: none;
  padding: 2px 12px;
  cursor: pointer;
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
  font-size: 1.1rem;
}

.react-tabs__tab--selected {
  background: rgb(179, 120, 55);
  border-color: #aaa;
  color: rgba(0, 0, 0, 0.8);
  border-radius: 5px 5px 0 0;
}

.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}

.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #fff;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}

@media (max-width: 800px) {
  .projects {
    margin-bottom: 5rem;
  }
}

.card-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem 2rem;
  justify-items: center;
  align-items: center;
  height: 25rem;
  width: 80%;
  margin: 2rem auto 8rem auto;
}

@media (max-width: 800px) {
  .card-wrapper {
    grid-template-columns: repeat(2, 1fr);
    height: auto;
  }
}

@media (max-width: 400px) {
  .card-wrapper {
    grid-template-columns: 1fr;
    height: auto;
  }
}

.card-container {
  height: auto;
  box-shadow: 0.4rem 0.2rem 0.4rem rgba(0, 0, 0, 0.5);
  background-color: rgba(0, 0, 0, 0.5);
}
.card-header img {
  width: 100%;
  height: 12rem;
}

.card-paragraph {
  font-size: 1.2rem;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}
.card-list {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.4rem;
}
.card-item {
  font-size: 1.2rem;
}
.card-footer-link {
  display: block;
  padding: 0.5rem 0;
  color: black;
  font-size: 1.3rem;
  border-bottom: 2px solid rgba(0, 0, 0, 0.5);
  background-color: white;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.card-footer-link:hover {
  background-color: rgb(179, 120, 55);
  color: rgba(255, 255, 255, 0.8);
}

@media (max-width: 800px) {
  .card-header img {
    width: 100%;
    height: 11rem;
  }

  .card-list {
    list-style: none;
    padding-left: 0.4rem;
  }
}

.footer {
  padding-top: 1rem;

  background-color: rgb(44, 44, 66);

  display: flex;
  flex-direction: column;
}

.footer-head {
  margin-bottom: 1rem;
  text-align: center;
}
.footer-head-text {
  text-align: center;
}
.footer-paragraph {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.3rem;
}

.footer-head {
  font-size: 2rem;
  color: rgba(255, 255, 255, 0.5);
}
.footer-details {
  font-size: 1.4rem;
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
}
.footer-text {
  margin-bottom: 3rem;
}

.highlight {
  color: rgb(228, 228, 74);
}

.footer-icons {
  display: flex;
  justify-content: center;
}

.footer-icon-link {
  background-color: rgb(179, 120, 55);
  padding: 1.8rem 2rem;
  font-size: 2rem;
  height: 3rem;
  border-radius: 10px;
  color: rgb(255, 255, 255, 0.7);
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}
.footer-icon-link:hover {
  cursor: pointer;
  background-color: rgb(228, 228, 74, 0.8);
  color: rgb(0, 0, 0, 0.8);
}

.footer-icon-link:not(:last-child) {
  margin-right: 2rem;
}

.footer-end {
  width: 100%;
  margin: 0 auto;
}
.footer-paragraph {
  text-align: center;
  margin-top: 3rem;
}

@media (max-width: 800px) {
  .footer-head {
    font-size: 1.4rem;
    width: 72%;
    text-align: center;
    margin: 1rem auto 1rem auto;
  }
}

